.form-callback {
	max-width: 480px;
	overflow: hidden;
	padding: 20px;
	position: relative;
	border-radius: 1rem;
	background-color: rgba(0, 0, 0, 0.5);
	@include trans;
	margin: auto;
	border: 1px solid #fff;
}

.form-callback h4 {
	color: #fff;
	font-family: "RobotoSlabRegular", sans-serif;
	text-align: center;
	@include font-size(24px);
	margin: 20px 0 5px 0;
	text-align: center;
	font-weight: 600;
	line-height: (27px / 20px);}

	.form-callback h5 {
		font-family: "RobotoSlabRegular", sans-serif;
		text-align: center;
		@include font-size(15px);
		line-height: (30px / 25px);
		color: #fff;
		margin-bottom: 20px;
	}
	.popup_input {
		display:block;
		width:96%;
		margin:10px auto 0;
		padding: 5px 0;
		@include font-size(16px);
		color:#000;
		border: none;
		border-bottom: 3px solid #f1c40f;
		text-indent:10px;
		@include trans;
	}

	.popup_input:focus, .popup_input:hover  {
		border-bottom: 3px solid #E5722C;
	}

	.popup_submit {
			font-family: "RobotoSlabBold", sans-serif;
		display:block;
		width:96%;
		border: none;
		padding: 7px 0;
		margin:10px auto 0;
		@include font-size(16px);
		cursor: pointer;
		border-bottom: 3px solid #f1c40f;
		background-color: #E5722C;
		@include trans;
	}
	.popup_submit:hover {
		border-bottom: 3px solid #E5722C;
		background-color: #f1c40f;
	}
.mfp-close-btn-in .mfp-close {
	color: #fff;
}


































