.checkbox_box {
	width: 100%;
	padding: 0;
	color: #fff;
	text-align: right;}

.checkbox_box input[type="checkbox"] {
	margin-left: 0;
	padding: 0; }

.checkbox_box input[type="checkbox"].css-checkbox {
position: absolute;
overflow: hidden;
clip: rect(0 0 0 0);
height: 1px;
width: 1px;
margin: -1px;
padding: 0px;
border: 0; }

.checkbox_box input[type="checkbox"].css-checkbox + label.css-label {
background-image: url("../images/checkbox_yes_white.png");
padding-left: 22px;
display: inline-block;
line-height: 15px;
background-repeat: no-repeat;
vertical-align: middle;
cursor: pointer;
opacity: 1;
color: #dedede;
@include font-size(12px);
line-height: 1.25; }
.checkbox_box input[type="checkbox"].css-checkbox + label.css-label:hover {
opacity: 1.0; }
.checkbox_box input[type="checkbox"]:checked + label.css-label {
background-image: url("../images/checkbox_no_white.png"); }
.css-label a {
color: #dedede;
@include trans;
}
.css-label a:hover {
color: #b2b0b0; }


.form-callback .checkbox_box {
   padding-top: 10px;
	width: 100%;
}


